import Parser from 'html-react-parser';
import Fade from 'react-reveal/Fade';
import React from 'react'

export default class About extends React.Component {
    render () {
      const property = {
          Title:
             'ABOUT',
        paragraphe1:
            'Created by three passionate of the game, Meta Manager is the number one NFT ecosystem based on football with a play to earn manager game online connected to a mind-blowing VR experience in fully crowded futuristic stadiums.',
        paragraphe2:
            'Off the pitch, own high-quality 3D Avatars with exclusive futuristic assets fully created and designed by our artists. the beginning of the sport fashion in the NFT space. Once on the pitch, manage a team wearing the colours of your club made of the Meta Players you collected. Lets bring football emotion to the Metaverse, the beautiful game will live forever.',
    };
 
    document.addEventListener("scroll", () => {
      if (window.scrollY >  window.innerWidth/500 + 250) {
      var test = document.getElementsByClassName("fade_text");
      var arr = Array.prototype.slice.call(test)
      arr.map((e) => 
        {
          e.style.animation = "fadein 2s";
          e.style.opacity = "1";
          return e;
        });
      }
    })
  
    return (<><section id="about">
    {/* <h1 className="rotate fade_text">
      {Parser(property.Title)}
    </h1> */}
    <div>
      <Fade left>
      <hr id="barre"/>
      </Fade>
      <p className="fade_text rotate1"> 
      {Parser(property.paragraphe1)}
      </p>
      <p className="fade_text">
      {Parser(property.paragraphe2)}  
      </p>
    </div>
  </section></>);
    }
}