import Fade from 'react-reveal/Fade';
import Parser from 'html-react-parser'
import React from 'react'

export default class Data extends React.Component {
  render() {
    const property = {
      paragraphe1:
        'In this first Meta Manager season',
      paragraphe2:
        'there will be 8888 META PLAYERS.',
    };
  
    document.addEventListener("scroll", () => {
      if (window.scrollY > window.innerWidth - 100) {
        var test = document.getElementsByClassName("test");
        var arr = Array.prototype.slice.call(test)
        arr.map((e) => {
          e.style.animation = "fadein 2s";
          e.style.opacity = "1";
          return e;
        });
        var anim = document.getElementsByClassName("anim");
        var array = Array.prototype.slice.call(anim)
        array.map((a) => {
          a.style.animation = "3s slidein";
          a.style.opacity = "1";
          return a;
        });
      }
    })

    return (<>   <section id="data">
    <div id="left">
    <Fade left>
        <h1>8888</h1>
        </Fade>
        <Fade right>
        <p>
        {Parser(property.paragraphe1)}
        </p>
        <p>
        {Parser(property.paragraphe2)}
        </p>
        </Fade>
    </div>
    <div id="right">
      <div className="Starters test">
        <p>Starters 50% </p>
      </div>
      <div className="Starters1">
        <img className="anim" alt="img1" src={require("./../img/lign_bleu.png")}/>
      </div>
      <div className="Good test">
        <p>Good 30% </p>
      </div>
      <div  className="Good1">
        <img className="anim" alt="img2" src={require("./../img/lign_bleu.png")}/>
      </div>
      <div className="Great test">
        <p>Great 15%</p>
      </div>
      <div className="Great1">
        <img className="anim" alt="img3" src={require("./../img/lign_bleu.png")}/>
      </div>
      <div className="SuperStar test">
        <p>SuperStar 4.8%</p>
      </div>
      <div  className="SuperStar1">
        <img className="anim" alt="img4" src={require("./../img/lign_bleu.png")}/>
      </div>
      <div className="Icon test">
        <p>Icon 0.2% </p>
      </div>
      <div  className="Icon1">
        <img className="anim" alt="img5" src={require("./../img/lign_bleu.png")}/>
      </div>
    </div>
  </section></>);
    }
}