export default function three() {
	window.THREE = THREE; // Used by APP Scripts.
	window.VRButton = VRButton; // Used by APP Scripts.

	var loader = new THREE.FileLoader();
	const clock = new THREE.Clock();
	var player = new APP.Player();
	loader.load('./app.json', function (text) {
		
		player.load(JSON.parse(text));
		player.setSize(window.innerWidth, window.innerHeight);
		player.play();

		document.body.appendChild(player.dom);

		window.addEventListener('scroll', function () {

		});

		// window.addEventListener('resize', function()
		// {
		//   var width = window.innerWidth;
		//   var height = window.innerHeight;
		//   renderer.setSize(width, height);
		//   camera.aspect = width / height;
		//   camera.updateProjectionMatrix();
		// });
		window.addEventListener('resize', function () {

			player.setSize( window.innerWidth, window.innerHeight );

		});

		function loopFunction(delay, callback) {
			var loop = function(){
				callback();
				setTimeout(loop, delay);
			}; loop();
		};
		
		loopFunction(1, function(){
			var delta = clock.getDelta();
			player.mixer.update(delta);
		});
		// pour faire l'animation de decalage
		document.getElementById("load").style.animation = "expand .7s ease forwards";
		// mettre le threejs visible
		document.getElementById("meta").style.display = "unset";
		// const scriptDesktop = document.createElement("script");
    	// const scriptMobile = document.createElement("script");

		// scriptDesktop.src = "./slider/slider-desktop.js";
		// scriptMobile.src = "./slider/slider-mobile.js";

		// scriptDesktop.type = "script";
		// scriptMobile.type = "script";

		// scriptDesktop.async = true;
		// scriptMobile.async = true;

		// document.body.appendChild(scriptDesktop);
		// document.body.appendChild(scriptMobile);
		},

	// onProgress callback
	function ( xhr ) {
		document.getElementById("chargement").innerText = Math.round(xhr.loaded / 24283865  * 100) + '%';
	});
}