import reportWebVitals from './reportWebVitals';
import { StrictMode } from "react";
import { createRoot } from "react-dom/client";

import App from "./App";

const rootElement = document.getElementById("root");
const root = createRoot(rootElement);
const scriptDesktop = document.createElement("script");
const scriptMobile = document.createElement("script");

scriptDesktop.src = "./slider/slider-desktop.js";
scriptMobile.src = "./slider/slider-mobile.js";

scriptDesktop.type = "script";
scriptMobile.type = "script";

scriptDesktop.async = true;
scriptMobile.async = true;

document.body.appendChild(scriptDesktop);
document.body.appendChild(scriptMobile);
root.render(
  <StrictMode>
    <App />
  </StrictMode>
);
// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
