import { EffectComposer } from './EffectComposer.js'
import { RenderPass } from 'three/examples/jsm/postprocessing/RenderPass.js'
import { CopyShader } from 'three/examples/jsm/shaders/CopyShader.js'
import { ShaderPass } from 'three/examples/jsm/postprocessing/ShaderPass.js'
import { DigitalGlitch } from './DigitalGlitch.js'
import { SepiaShader } from 'three/examples/jsm/shaders/SepiaShader.js'
import { GlitchPass } from './GlitchPass.js'
import { FXAAShader } from 'three/examples/jsm/shaders/FXAAShader.js'
import { GammaCorrectionShader } from 'three/examples/jsm/shaders/GammaCorrectionShader.js'
import { LuminosityShader } from 'three/examples/jsm/shaders/LuminosityShader.js'
import { RGBShiftShader } from 'three/examples/jsm/shaders/RGBShiftShader.js'
import * as THREE from 'three'

var APP = {

	Player: function () {

		var renderer = new THREE.WebGLRenderer( { antialias: true} );
		renderer.setPixelRatio( window.devicePixelRatio ); // TODO: Use player.setPixelRatio()
		renderer.outputEncoding = THREE.sRGBEncoding;
		renderer.gammaFactor = 2.2
		var loader = new THREE.ObjectLoader();
		var camera, scene, mixer;
		const clock = new THREE.Clock();
		this.mixer = mixer;
		var vrButton = VRButton.createButton( renderer ); // eslint-disable-line no-undef
		var composer = new EffectComposer(renderer);
		camera = new THREE.PerspectiveCamera(45, this.width / this.height, 1, 1000);
        camera.position.x = 0;
        camera.position.y = 1.8;
        camera.position.z = 1.5 + window.scrollY / 300.0;
        camera.lookAt(0, 1.5, 0);

        function updateCamera(ev) {
            let div1 = document.getElementById("div");
            camera.position.z = 1.5 + window.scrollY / 300.0;
        }

		window.addEventListener("scroll", updateCamera);

		window.addEventListener( 'resize', onWindowResize, false );

		function onWindowResize() {
			composer.setSize(window.innerWidth, window.innerHeight);

			camera.aspect = window.innerWidth / window.innerHeight;
			camera.updateProjectionMatrix();
		}

		var events = {};

		var dom = document.createElement( 'div' );
		dom.appendChild( renderer.domElement );

		this.dom = dom;
		dom.childNodes[0].style.zIndex = -1;
		this.pixelRatio = window.devicePixelRatio || 1;
		this.width = 500;
		this.height = 500;

		this.load = function (json) {

			var project = json.project;

			if ( project.vr !== undefined ) renderer.xr.enabled = project.vr;
			if ( project.shadows !== undefined ) renderer.shadowMap.enabled = project.shadows;
			if ( project.shadowType !== undefined ) renderer.shadowMap.type = project.shadowType;
			if ( project.toneMapping !== undefined ) renderer.toneMapping = project.toneMapping;
			if ( project.toneMappingExposure !== undefined ) renderer.toneMappingExposure = project.toneMappingExposure;
			if ( project.physicallyCorrectLights !== undefined ) renderer.physicallyCorrectLights = project.physicallyCorrectLights;
			
			var tester = loader.parse(json.scene)
			mixer = new THREE.AnimationMixer(tester);
			mixer.timeScale = 0.3;
			json.scene.animations.forEach((clip) => {
				let tracks = []
				clip.tracks.map((anim) => {
					if (anim.type == "vector") {
						tracks.push(new THREE.VectorKeyframeTrack(anim.name, anim.times, anim.values));
					} else if (anim.type == "quaternion") {
						tracks.push(new THREE.QuaternionKeyframeTrack(anim.name, anim.times, anim.values));
					} else 
						console.log("error")
				})
				mixer.clipAction(new THREE.AnimationClip(
					clip.name, clip.duration, tracks
				), tester).play();
			});
			this.mixer = mixer;
			this.setScene(tester);

			var renderPass = new RenderPass(scene, camera);
			var pass1 = new GlitchPass(64);
			var pass2 = new ShaderPass(DigitalGlitch);
			var effectFXAA = new ShaderPass(FXAAShader);
			var gamma = new ShaderPass(GammaCorrectionShader);
			var copyPass = new ShaderPass( CopyShader );
			pass2.uniforms.byp.value = 1;
			pass2.uniforms.amount.value = 0.03;
			pass2.uniforms.distortion_x.value = 0.1;
			pass2.uniforms.distortion_y.value = 0.1;
			
			effectFXAA.uniforms[ 'resolution' ].value.x = 1 / ( window.innerWidth * this.pixelRatio );
			effectFXAA.uniforms[ 'resolution' ].value.y = 1 / ( window.innerHeight * this.pixelRatio );
			composer.addPass(renderPass);
			composer.addPass(pass1);
			composer.addPass(pass2);
			composer.addPass(effectFXAA);
			composer.addPass(gamma);
			composer.addPass(copyPass);
			composer.setSize(window.innerWidth, window.innerHeight);
			copyPass.renderToScreen = true;
			
			document.addEventListener('mousemove', animateParticles)
			let mouseX = 0
			let mouseY = 0
			const particlesGeometry = new THREE.BufferGeometry;
			const particlesCnt = 3000;
			const posArray = new Float32Array(particlesCnt * 3);
			for(let i = 0; i < particlesCnt * 3; i++) {
				posArray[i] = (Math.random() - 0.5) * 15;
			}
			particlesGeometry.setAttribute('position', new THREE.BufferAttribute(posArray, 3))
			const material = new THREE.PointsMaterial({
				size: 0.009,
				color: 0x0400ff,
				opacity: 1
			});
			const particlesMesh = new THREE.Points(particlesGeometry, material)
			function animateParticles(event) {
				mouseX = event.clientX
				mouseY = event.clientY
			}
			const clock = new THREE.Clock();
			const tick = () =>
			{
				const elapsedTime = clock.getElapsedTime();
				particlesMesh.rotation.y = -.1 * elapsedTime 
				if (mouseX > 0) {
					particlesMesh.rotation.x = -mouseY * (elapsedTime * 0.00008)
					particlesMesh.rotation.y = mouseX * (elapsedTime * 0.00008)
				}
				window.requestAnimationFrame(tick)
			}
			tick();
			scene.add(particlesMesh);

			document.addEventListener('mousemove', WanimateParticles)
			const WparticlesGeometry = new THREE.BufferGeometry;
			const WparticlesCnt = 3000;
			const WposArray = new Float32Array(WparticlesCnt * 3);
			for(let i = 0; i < WparticlesCnt * 3; i++) {
				WposArray[i] = (Math.random() - 0.5) * 15;
			}
			WparticlesGeometry.setAttribute('position', new THREE.BufferAttribute(WposArray, 3))
			const Wmaterial = new THREE.PointsMaterial({
				size: 0.009,
				color: 0xffffff
			});
			const WparticlesMesh = new THREE.Points(WparticlesGeometry, Wmaterial)
			function WanimateParticles(event) {
				mouseX = event.clientX
				mouseY = event.clientY
			}
			const Wclock = new THREE.Clock();
			const Wtick = () =>
			{
				const elapsedTime = Wclock.getElapsedTime();
				WparticlesMesh.rotation.y = -.1 * elapsedTime 
				if (mouseX > 0) {
					WparticlesMesh.rotation.x = -mouseY * (elapsedTime * 0.00008)
					WparticlesMesh.rotation.y = mouseX * (elapsedTime * 0.00008)
				}
				window.requestAnimationFrame(Wtick)
			}
			Wtick();
			scene.add(WparticlesMesh);

			events = {
				init: [],
				start: [],
				stop: [],
				keydown: [],
				keyup: [],
				pointerdown: [],
				pointerup: [],
				pointermove: [],
				update: []
			};
			var scriptWrapParams = 'player,renderer,scene,camera';
			var scriptWrapResultObj = {};

			for ( var eventKey in events ) {

				scriptWrapParams += ',' + eventKey;
				scriptWrapResultObj[ eventKey ] = eventKey;

			}

			var scriptWrapResult = JSON.stringify( scriptWrapResultObj ).replace( /\"/g, '' );

			for ( var uuid in json.scripts ) {

				var object = scene.getObjectByProperty( 'uuid', uuid, true );

				if ( object === undefined ) {

					console.warn( 'APP.Player: Script without object.', uuid );
					continue;

				}

				var scripts = json.scripts[ uuid ];

				for ( var i = 0; i < scripts.length; i ++ ) {

					var script = scripts[ i ];

					var functions = ( new Function( scriptWrapParams, script.source + '\nreturn ' + scriptWrapResult + ';' ).bind( object ) )( this, renderer, scene, camera );

					for ( var name in functions ) {

						if ( functions[ name ] === undefined ) continue;

						if ( events[ name ] === undefined ) {

							console.warn( 'APP.Player: Event type not supported (', name, ')' );
							continue;

						}

						events[ name ].push( functions[ name ].bind( object ) );

					}

				}

			}

			dispatch( events.init, arguments );

		};

		this.setCamera = function ( value ) {

			camera = value;
			camera.aspect = this.width / this.height;
			camera.updateProjectionMatrix();

		};

		this.setScene = function ( value ) {

			scene = value;

		};

		this.setPixelRatio = function ( pixelRatio ) {

			renderer.setPixelRatio( pixelRatio );

		};

		this.setSize = function ( width, height ) {

			this.width = width;
			this.height = height;

			if ( camera ) {

				camera.aspect = this.width / this.height;
				camera.updateProjectionMatrix();

			}

			renderer.setSize( width, height );

		};

		function dispatch( array, event ) {

			for ( var i = 0, l = array.length; i < l; i ++ ) {

				array[ i ]( event );

			}

		}

		var time, startTime, prevTime;

		function animate() {

			time = performance.now();

			try {

				dispatch( events.update, { time: time - startTime, delta: time - prevTime } );

			} catch ( e ) {

				console.error( ( e.message || e ), ( e.stack || '' ) );

			}
			composer.render();
			//renderer.render( scene, camera );

			prevTime = time;

		}

		this.play = function () {
			if ( renderer.xr.enabled ) dom.append( vrButton );
			var delta = clock.getDelta();
			mixer.update(delta);
			startTime = prevTime = performance.now();

			document.addEventListener( 'keydown', onKeyDown );
			document.addEventListener( 'keyup', onKeyUp );
			document.addEventListener( 'pointerdown', onPointerDown );
			document.addEventListener( 'pointerup', onPointerUp );
			document.addEventListener( 'pointermove', onPointerMove );

			dispatch( events.start, arguments );

			renderer.setAnimationLoop( animate );

		};

		this.stop = function () {

			if ( renderer.xr.enabled ) vrButton.remove();

			document.removeEventListener( 'keydown', onKeyDown );
			document.removeEventListener( 'keyup', onKeyUp );
			document.removeEventListener( 'pointerdown', onPointerDown );
			document.removeEventListener( 'pointerup', onPointerUp );
			document.removeEventListener( 'pointermove', onPointerMove );

			dispatch( events.stop, arguments );

			renderer.setAnimationLoop( null );

		};

		this.render = function ( time ) {

			dispatch( events.update, { time: time * 1000, delta: 0 /* TODO */ } );

			renderer.render( scene, camera );

		};

		this.dispose = function () {

			renderer.dispose();

			camera = undefined;
			scene = undefined;

		};

		//

		function onKeyDown( event ) {

			dispatch( events.keydown, event );

		}

		function onKeyUp( event ) {

			dispatch( events.keyup, event );

		}

		function onPointerDown( event ) {

			dispatch( events.pointerdown, event );

		}

		function onPointerUp( event ) {

			dispatch( events.pointerup, event );

		}

		function onPointerMove( event ) {

			dispatch( events.pointermove, event );

		}

	}

};

export { APP };
